.codepen {
    height: 300px; 
    box-sizing: border-box; 
    display: flex; 
    align-items: center; 
    justify-content: center; 
    border: 2px solid; 
    margin: 1em 0; 
    padding: 1em;
}

iframe {
    width: 100%;
}

@media screen and (max-width:1024px) {
    .main {
        margin: 0 auto;
    }
}

ol {
    list-style-type:decimal;
}