pre {
  background-color: #f4f4f4;
  padding: 10px;
  font-size: 14px;
  border-radius: 5px;
  overflow-x: auto;
}

.code {
  background-color: #f4f4f4;
  padding: 3px;
  font-size: 14px;
}

@media screen and (max-width:550px) {
  .main {
    max-width: 400px;
  }

  @media screen and (max-width:400px) {
    .main {
      max-width:350px;
    }
  }
}