.title {
  font-weight: 700;
  font-size: 2rem;
}

@font-face {
  font-family: Montserrat;
  src: url(../font/Montserrat-Regular.ttf);
  font-weight: 300;
}

@font-face {
  font-family: Montserrat;
  src: url(../font/Montserrat-Medium.ttf);
  font-weight: 700;
}

* {
  font-family: "Montserrat";
  box-sizing: border-box;
}

.bold {
  font-weight: 700;
}

pre {
  background-color: #f4f4f4;
  padding: 10px;
  font-size: 14px;
  border-radius: 5px;
  overflow-x: auto;
}

.code {
  background-color: #f4f4f4;
  padding: 3px;
  font-size: 14px;
}

@media screen and (max-width:550px) {
  .main {
    max-width: 400px;
  }

  @media screen and (max-width:400px) {
    .main {
      max-width:350px;
    }
  }
}

iframe {
  width: 100%;
}
ol {
  list-style-type:decimal;
}